import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { contactUs } from '../contactUs';
import { ApiDataServiceService } from './api-data-service.service';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {
  public contactForm: FormGroup;
  public mycontact:contactUs;
  public successMsg:string = "";  
  public errorMsg:string = "";
  public Loading:boolean = false;

  get firstname() {
    return  this.contactForm.get('firstname');
   }
   get lastname() {
    return  this.contactForm.get('lastname');
   }
  get email() {
   return  this.contactForm.get('email');
  }
  get subject() {
    return  this.contactForm.get('subject');
  }
  get notes() {
    return  this.contactForm.get('notes');
   }



  constructor(private fb:FormBuilder, private _ApiDataServiceService: ApiDataServiceService) { }

  ngOnInit() {
    this.contactForm = this.fb.group({
      firstname: ['', [Validators.required, Validators.minLength(3),Validators.maxLength(10)]],
      lastname: ['', [Validators.required, Validators.minLength(3),Validators.maxLength(10)]],
      email: ['', [Validators.required,Validators.email, Validators.maxLength(50)]],
      subject: ['', [Validators.required,Validators.maxLength(50)]],
      notes: ['', [Validators.required,Validators.maxLength(100)]]
    });

    this.contactForm.valueChanges.subscribe(value =>
      {
       // console.log(this.contactForm);
      });
  }

  onSubmit() {   
    if (this.contactForm.valid)
    {
      this.Loading = true;      
      this.mycontact = this.contactForm.value;
      console.log(this.mycontact);
      this._ApiDataServiceService.SaveContactUs(this.mycontact).subscribe(res =>
        {
          this.successMsg = "Successfully submit your request. Somebody from Trinet Software will soon contact you via email.";
          this.Loading = false;
        },
        err =>{  this.errorMsg = "There is some internal error. Still, somebody will contact you soon.";
                this.Loading = false;
              }
       );       
       this.contactForm.reset();
    }
    
    
  }

}
